@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap);
/* Start Global Varibales  */
:root {
  --main-color: hsl(26, 100%, 55%);
  --transition-period: 0.3s;
  --secondary-color: hsl(219, 9%, 45%);
  --section-background: #f6f6f6;
}

/* End Global Varibales  */

/* Start Global Rules */
*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  border: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
html {
  -webkit-scroll-behavior: smooth;
  -moz-scroll-behavior: smooth;
  scroll-behavior: smooth;
}
body {
  font-family: "Kumbh Sans", sans-serif;
  background-color: hsl(0, 0%, 100%);
  -webkit-zoom: 80%;
  -moz-zoom: 80%;
  zoom: 80%;
}
/* Small */
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
/* Medium */
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
/* Large */
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}
.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}
/* End Global Rules */

/* Start Header  */

header .container {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .container::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 15px;
  height: 1px;
  width: calc(100% - 30px);
  background-color: hsl(223, 64%, 92%);
}
nav {
  position: relative;
  display: flex;
  flex: 1 1;
}
nav a {
  display: block;
  padding: 40px 0px;
  color: hsl(219, 9%, 45%);
  margin-right: 25px;
  font-size: 15px;
  letter-spacing: 1px;
  transition: var(--transition-period);
  border-bottom: 5px solid transparent;
  z-index: 2;
}
nav a:hover {
  color: hsl(219, 9%, 25%);
  font-weight: 600;
  border-bottom: 5px solid var(--main-color);
}
header .cart {
  width: 50px;
  cursor: pointer;
}
header .cart,
header .logo,
header span {
  display: block;
}
.logo {
  width: 230px;
}
header span {
  max-width: 50px;
  max-height: 50px;
  transition: var(--transition-period);
  border-radius: 50%;
  border: 2px solid transparent;
  box-sizing: content-box;
  cursor: pointer;
}
header span:hover {
  border: 2px solid var(--main-color);
}
header span img {
  max-width: 100%;
  max-height: 100%;
}
.bars,
.exit {
  display: none;
  cursor: pointer;
}
.exit:hover path {
  fill: var(--main-color);
}
/*------------ Responsive -----------*/
@media (max-width: 767px) {
  header .container::after {
    content: "";
    display: none;
  }
  header nav {
    display: none;
  }
  .bars {
    display: block;
  }
  .exit {
    display: block;
    position: absolute;
    top: 10px;
    left: 20px;
  }
  .logo {
    margin-left: 20px;
    flex: 1 1;
  }
  header nav a:first-of-type {
    margin-top: 65px;
  }
  header nav a {
    padding: 10px 20px;
    margin: 0;
    font-weight: 800;
    color: black;
  }
}
/* ---------------End Header------------------- */

/*----------------- Start Product Details -------------*/
.product-details .container {
  padding: 70px 70px 30px;
  display: flex;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
}
/*-------------Responsive----------------*/
@media (max-width: 991px) {
  .product-details .container {
    padding-top: 30px;
    flex-direction: column;
  }
}
@media (max-width: 767px) {
  .product-details .container {
    padding: 10px 25px;
  }
}
/*----------------- End Product Details -------------*/

/*----------------- Start Product Description-------------*/
.product-description {
  margin-top: 60px;
  width: 47%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.product-description .company-name {
  text-transform: uppercase;
  color: var(--main-color);
  font-size: small;
  letter-spacing: 1px;
  font-weight: 700;
}
.product-description .product-name {
  margin-top: 20px;
  font-size: 40px;
}
.product-description .description {
  margin-top: 30px;
  color: var(--secondary-color);
  line-height: 1.6;
}
.product-description .price {
  margin-top: 20px;
  position: relative;
}
.product-description .price :not(span:last-child) {
  display: inline-block;
}
.product-description .price .actual {
  font-size: 25px;
  font-weight: bold;
  margin-right: 10px;
  letter-spacing: 1.5px;
}
.product-description .price .discount {
  color: var(--main-color);
  background-color: hsl(25, 100%, 94%);
  padding: 1px 6px;
  font-weight: bold;
  -webkit-transform: translateY(-4px);
          transform: translateY(-4px);
}
.product-description .price .old {
  display: block;
  margin-top: 10px;
  text-decoration: line-through;
  color: hsl(220, 14%, 75%);
  font-weight: bold;
}
/*-------------Responsive----------------*/

@media (max-width: 991px) {
  .product-description {
    margin-top: 5px;
    width: 100%;
    justify-content: center;
  }
  .product-description .product-name {
    margin-top: 13px;
  }
  .product-description .description {
    margin-top: 10px;
  }

  .product-description .price .old {
    display: inline-block;
    margin-left: 30px;
    margin-top: 0;
    margin-bottom: 10px;
  }
}
@media (max-width: 767px) {
  .product-description .product-name {
    font-size: 35px;
  }
  .product-description .description {
    font-size: 17px;
  }
  .product-description .price {
    width: 100%;
  }
  .product-description .price .old {
    position: absolute;
    right: -10px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    font-size: 15px;
  }
}
/*----------------- End Product Description-------------*/

/*----------------- Start Add To Cart-------------*/
.add-to-cart {
  width: 450px;
  display: flex;
  align-items: center;
  margin-top: 20px;
}
.add-to-cart div {
  width: 160px;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  background-color: hsl(223, 64%, 98%);
  margin-right: 15px;
}
.add-to-cart div svg {
  cursor: pointer;
}
.add-to-cart div * {
  display: block;
  font-weight: bold;
}
.add-to-cart button {
  flex: 1 1;
  padding: 20px;
  color: white;
  font-weight: bold;
  background-color: var(--main-color);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.add-to-cart button:hover {
  opacity: 0.5;
}
.add-to-cart button * {
  display: block;
}
.add-to-cart button span {
  font-size: 15px;
  margin-left: 10px;
}
.add-to-cart button svg {
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}
.add-to-cart button svg path {
  fill: white;
}

/*-------------Responsive----------------*/

@media (max-width: 991px) {
  .add-to-cart {
    width: 100%;
    margin-top: 10px;
  }
  .add-to-cart button {
    width: 60%;
    flex: unset;
  }
}
@media (max-width: 767px) {
  .add-to-cart {
    width: 80%;
    align-self: center;
    flex-wrap: wrap;
  }
  .add-to-cart div {
    width: 100%;
  }

  .add-to-cart button {
    width: 100%;
  }
}
/*----------------- End Add To Cart-------------*/

/*----------------- Start Product Preview -------------*/
.product-preview {
  width: 45%;
  display: flex;
  flex-wrap: wrap;
}
.product-preview img {
  cursor: pointer;
}

.product-preview .current-image {
  margin-bottom: 15px;
  flex-basis: 100%;
  position: relative;
}
.product-preview .current-image img {
  max-width: 100%;
  max-height: 100%;
  border-radius: 13px;
}

.product-preview .all-images {
  flex: 1 1;
  display: flex;
  justify-content: space-between;
}
.product-preview .all-images div {
  display: flex;
  justify-content: center;
  align-items: center;
  width: calc(90% / 4);
  height: 90px;
  border-radius: 13px;
}
.product-preview .all-images img {
  width: 100%;
  height: 100%;
  border-radius: 10px;
}
.product-preview .all-images div.active {
  border: 3px solid var(--main-color);
}
.product-preview .all-images div.active img,
.product-preview .all-images img:hover {
  opacity: 0.5;
}
.next,
.previous {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: white;
  display: flex;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.next {
  right: 40px;
}
.previous {
  left: 40px;
}
.next:hover svg,
.previous:hover svg {
  color: var(--main-color);
}
.next svg,
.previous svg {
  font-size: 1.5em;
}
/*-------------Responsive----------------*/

@media (max-width: 991px) {
  .product-preview {
    justify-content: center;
    width: 100%;
  }
  .product-preview img {
    cursor: auto;
  }
  .product-preview .all-images {
    display: none;
  }
  .product-preview .current-image {
    height: 500px;
    text-align: center;
  }
  .product-preview .current-image img {
    width: 90%;
  }
}
@media (max-width: 767px) {
  .product-preview {
    -webkit-transform: scaleX(1.3);
            transform: scaleX(1.3);
  }
  .product-preview .current-image img {
    width: 100%;
    height: 100%;
    border-radius: 0;
  }
}
/*----------------- End Product Preview -------------*/

/*----------------- Start lightBox -------------*/
.dark-bk {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 50%;
  left: 50%;
  z-index: 900;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: rgb(0, 0, 0, 70%);
}

.lightBox {
  z-index: 1000;
  position: absolute;
  top: 57%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.lightBox .product-preview {
  width: 450px;
}
.lightBox .product-preview .current-image {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.lightBox .product-preview .all-images {
  margin-top: 30px;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
.lightBox .product-preview .all-images div {
  width: calc(80% / 4);
}
.lightBox .product-preview .all-images div {
  background-color: white;
}
.lightBox .exit-lightBox {
  position: absolute;
  top: -50px;
  right: -15px;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}
.lightBox .exit-lightBox path {
  fill: white;
  cursor: pointer;
}
.lightBox .exit-lightBox path:hover {
  fill: var(--main-color);
}
.lightBox .next,
.lightBox .previous {
  top: 40%;
}
.lightBox .next {
  right: -50px;
}
.lightBox .previous {
  left: -50px;
}

/*----------------- End lightBox -------------*/

